interface ArsenalProps {
  onBack: () => void;
}

const Arsenal: React.FC<ArsenalProps> = ({ onBack }) => {
  const [selectedService, setSelectedService] = useState<Service | null>(null);

  const handleServiceClick = (service: Service) => {
    setSelectedService(service);
  };

  const handleClose = () => {
    setSelectedService(null);
  };

  const handleOverlayClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      handleClose();
    }
  };

  const handleServiceOpen = (url: string) => {
    window.Telegram.WebApp.openLink(url);
  };

  return (
    <Container>
      <BackButton onClick={onBack}></BackButton>
      
      <Header>
        <HeaderTitle>  </HeaderTitle>
        <HeaderDescription>
          , , .     ,      .
        </HeaderDescription>
      </Header>

      {categories.map((category, index) => (
        <Category key={index}>
          <CategoryTitle>{category.title}</CategoryTitle>
          <ServiceGrid>
            {category.services.map((service, serviceIndex) => (
              <ServiceCard 
                key={serviceIndex}
                onClick={() => handleServiceClick(service)}
              >
                <ServiceIcon>{service.icon}</ServiceIcon>
                <ServiceName>{service.name}</ServiceName>
              </ServiceCard>
            ))}
          </ServiceGrid>
        </Category>
      ))}

      <Overlay isVisible={!!selectedService} onClick={handleOverlayClick}>
        {selectedService && (
          <OverlayCard isVisible={!!selectedService} onClick={(e) => e.stopPropagation()}>
            <CloseButton onClick={handleClose}></CloseButton>
            <OverlayTitle>
              <ServiceIcon>{selectedService.icon}</ServiceIcon>
              {selectedService.name}
            </OverlayTitle>
            <OverlayDescription>
              {selectedService.description}
            </OverlayDescription>
            <OverlayButton onClick={() => handleServiceOpen(selectedService.url)}>
               {selectedService.name}
            </OverlayButton>
          </OverlayCard>
        )}
      </Overlay>
    </Container>
  );
};

export default Arsenal; 