import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';

declare global {
  interface Window {
    Telegram: {
      WebApp: any;
    };
  }
}

const tg = window.Telegram.WebApp;

//  Telegram WebApp
tg.ready();
tg.expand();

//  
document.body.style.backgroundColor = 'var(--tg-theme-bg-color, #000000)';
document.body.style.color = 'var(--tg-theme-text-color, #ffffff)';

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);

root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
); 